*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
  background-color:rgb(168, 192, 235)
}

.heading_style{
  padding:30px 0px;
  text-align: center;
  justify-content: center;
  font-family:Arial, Helvetica, sans-serif;
  background-color: white;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 2px;
  color:rgb(72, 99, 148);
  margin-bottom: 30px;
}


.cards{
  width:100%;
  height:auto;
  justify-content: center;
  text-align:left;

}

.card{
  margin:6%;
  background-color: #fff;
  width:21.25%;
  float:left;
  border-radius: 12px ;
  box-shadow: 0px 13px 10px -7px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
}
.card:hover{
  box-shadow:0px 30px 18px -8px rgba(0 , 0, 0, 0.1);
  transform:scale(1.05, 1.05);

}
.card_img{
   width:100%;
 height:350px;
border-top-left-radius: 12px;
border-top-right-radius:12px; ;
}

.card_info{
  background-color: #fff;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}

.card_category{
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color:#605f5f;
  background-color: #fff;
}

.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  background-color: #fff;
  text-transform: capitalize;
}

button{
  padding:5px 8px;
  font-family:  Georgia,'Times New Roman', Times, serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 650;
  color:rgb(105, 121, 150);
  background-color: #fff;
  outline:none;
  border:ipx solid black;
  cursor: pointer;
}

@media (max-width:768px){
  .card{
    float: none;
    margin: 5%;
    width: 90%;
    align-items: center;
    
  }
}

